import React, { Component } from "react";
import { Select } from 'antd';
import Apis from '@/apis'
const { Option, OptGroup } = Select;

class View extends Component {
  constructor(props) {
    super(props)
    this.state = {
      selectOptions: props.options || [],
      groupList: []
    }
  }

  componentDidMount() {
    this.fetchData()
  }

  fetchData(data = {}) {
    const { titlepropname, valuepropname, grouppropname, params, api } = this.props
    if (Apis[api]) {
      Apis[api]({
        ...params || {},
        ...data
      }).then(res => {
        if (res && Array.isArray(res)) {
          res.forEach(item => {
            item.group = grouppropname ? item[grouppropname] : ''
            item.label = titlepropname ? item[titlepropname] : (item.title || item.name)
            item.value = valuepropname ? item[valuepropname] : item._id
          })
          let groupList = []
          for (let item of res) {
            if (item.group) {
              let children = res.filter(_item => _item.group === item.group)
              if (children.length && !groupList.find(group => group.label === item.group)) {
                groupList.push({ label: item.group, children })
              }
            }
          }
          this.setState({
            selectOptions: res,
            groupList
          })
        }
      })
    }
  }

  onChange = value => {
    // console.log(value);
    const { onChange } = this.props
    if (onChange) onChange(value)
  };

  onSearch = value => {
    let data = {}
    const { searchname, onSearch } = this.props
    data[`${searchname}_contains`] = value
    this.fetchData(data)
    if (onSearch) onSearch(value)
  };

  render() {
    const { value, onChange, showSearch, options, filterOption, ...otherProps } = this.props
    const { selectOptions, groupList } = this.state;
    return (
      <Select
        style={{ width: '100%' }}
        value={value}
        dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
        placeholder="请选择"
        onChange={this.onChange}
        showSearch={!!showSearch}
        onSearch={(!!showSearch && !filterOption) ? this.onSearch : false}
        filterOption={filterOption}
        {...otherProps}
      >
        {
          groupList.length ? (
            groupList.map((group, i) => (
              <OptGroup label={group.label} key={i}>
                {
                  group.children.length && group.children.map((item, index) => (
                    <Option key={`${i}_${index}`} value={item.value}>{item.label}</Option>
                  ))
                }
              </OptGroup>
            ))
          ) : (
            selectOptions.map((item, index) => (
              <Option key={index} value={item.value}>{item.label}</Option>
            ))
          )
        }
      </Select>
    );
  }
}
export default View